import React from 'react';
import { Link } from 'react-router-dom';
import styles from './styles.module.css';
import { ROUTES } from '../../consts/routes';
import Team from '../../models/Team';

type HeaderProps = {
    team: Team | null;
    isTeamLoading: boolean;
    logout: () => void;
}

export default function Header(props: HeaderProps) {
    const isLoggedIn = Boolean(props.team);
    return (
        <header className={ styles.root }>
            <h1>
                <Link to='/' className={ styles.titleLink }>ICFP Programming Contest 2020</Link>
            </h1>
            <div className={ styles.links }>
                { !props.isTeamLoading &&
                <>
                    { isLoggedIn ? renderAuthorizedLinks(props) : renderUnauthorizedLinks() }
                </>
                }
            </div>
        </header>
    )
}

function renderUnauthorizedLinks() {
    return (
        <>
            <Link to={ ROUTES.registration } className={ styles.link }>
                <span className={ styles.note }>
                    &nbsp;Hey!&nbsp;→
                </span>
                Register your team
            </Link>
            <Link to={ ROUTES.login } className={ styles.link }>Log in</Link>
        </>
    )
}

function renderAuthorizedLinks(props: HeaderProps) {
    const { team, logout } = props;
    if (!team) return;
    return (
        <>
            <Link to={ ROUTES.teamInfo } className={ styles.link }>View profile</Link>
            <Link to={ ROUTES.submissions } className={ styles.link }>View submissions</Link>
            <span><button onClick={ logout } className={ styles.logoutLink }>Log out</button></span>
        </>
    )
}