import React from 'react';
import styles from './styles.module.css';
import contestStyles from '../ContestCards/styles.module.css';
import { contests, getLanguageRating } from '../../../consts/contests'

const minYear = contests.reduce((year, contest) => {
    return year === -1 ? contest.year : Math.min(year, contest.year)
}, -1)

const maxYear = contests.reduce((year, contest) => {
    return year === -1 ? contest.year : Math.max(year, contest.year)
}, -1)

const rating = getLanguageRating().slice(0, 10)

export default function LanguageRating() {
    return (
        <div className={ styles.root }>
            <div className={ styles.rating }>
                <h3>All-Time Language Rating</h3>

                <div className={ styles.timeframe }>{minYear} — {maxYear}</div>

                <ul className={ styles.languages }>
                    <li key="header" className={ styles.header }>
                        <div/>
                        <div className={ contestStyles.languages__first }>1st</div>
                        <div className={ contestStyles.languages__second }>2nd</div>
                        <div className={ contestStyles.languages__third }>3rd</div>
                        <div className={ contestStyles.languages__lightning }>
                            <span role="img" aria-label="lightning division">⚡️</span>
                        </div>
                    </li>
                    {rating.map(([ language, scores ], i) => (
                        <li key={language} className={ styles.entry }>
                            <div className={ styles.language }>{language}</div>
                            <div>{scores.first || ""}</div>
                            <div>{scores.second || ""}</div>
                            <div>{scores.third || ""}</div>
                            <div>{scores.lightning || ""}</div>
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    )
}
