import React, { useState } from 'react';
import styles from './styles.module.css';
import { contests, getClaim } from '../../../consts/contests';

function shuffle<T>(array: Array<T>): Array<T> {
    const copyArr = [...array];
    let currentIndex = array.length
    let temporaryValue, randomIndex

    while (0 !== currentIndex) {
        randomIndex = Math.floor(Math.random() * currentIndex)
        currentIndex -= 1

        temporaryValue = copyArr[currentIndex]
        copyArr[currentIndex] = copyArr[randomIndex]
        copyArr[randomIndex] = temporaryValue
    }

    return copyArr
}

function getRandomContest() {
    return shuffle(contests).pop()
}

export default function ContestCards() {
    const [ state, updateState ] = useState({
        contest: getRandomContest()
    })

    const contest = state.contest

    function update() {
        updateState({
            contest: getRandomContest()
        })
    }
    
    if (!contest) return null;

    return (
        <div className={ styles.root }>
            <div className={ styles.card }>
                <div>
                    <h3>ICFP Contest { contest.year }</h3>

                    <div className={ styles.organizer }>{ contest.organizer }</div>

                    <div className={ styles.problem }>{ contest.problem }</div>

                    <ul className={ styles.languages }>
                        { contest.languages.first && (
                            <div className={ styles.languages__first } title={ getClaim("first", contest.languages.first) }>
                                { contest.languages.first }
                            </div>
                        ) }

                        { contest.languages.second && (
                            <div className={ styles.languages__second } title={ getClaim("second", contest.languages.second) }>
                                { contest.languages.second }
                            </div>
                        ) }

                        { contest.languages.third && (
                            <div className={ styles.languages__third } title={ getClaim("third", contest.languages.third) }>
                                { contest.languages.third }
                            </div>
                        ) }

                        { contest.languages.lightning && (
                            <div className={ styles.languages__lightning } title={ getClaim("lightning", contest.languages.lightning) }>
                                { contest.languages.lightning }
                            </div>
                        ) }
                    </ul>
                </div>

                <div className={ styles.shuffleLink }>
                    <button className={ styles.linkButton } onClick={ update }>Show another year</button>
                </div>
            </div>
        </div>
    )
}
