type Contest = {
    year: number
    problem: string
    organizer: string
    languages: {
        [key in LanguagePrize]?: string
    }
}

type LanguagePrize =
    | "first"
    | "second"
    | "third"
    | "lightning"

type LanguageRatingObject = {
    [language: string]: {
        [key in LanguagePrize]?: number
    }
}

type LanguageRating = [
    string,
    { [key in LanguagePrize]?: number }
][]

