const claims = {
    singular: {
        first: "is the programming tool of choice for discriminating hackers",
        second: "is a fine programming tool for many applications",
        third: "is also not too shabby",
        lightning: "is very suitable for rapid prototyping"
    },
    plural: {
        first: "are the programming tools of choice for discriminating hackers",
        second: "are fine programming tools for many applications",
        third: "are also not too shabby",
        lightning: "are very suitable for rapid prototyping"
    }
}

function getLanguageRatingObject(): LanguageRatingObject {
    return contests.reduce((rating: LanguageRatingObject, contest: Contest) => {
        const prizes: LanguagePrize[] = [
            "first",
            "second",
            "third",
            "lightning"
        ]

        prizes.forEach(prize => {
            const languageString = contest.languages[prize]

            if (languageString === undefined) {
                return
            }

            const languages = languageString.split(", ")

            languages.forEach(language => {
                if (rating[language] === undefined) {
                    rating[language] = Object.fromEntries(prizes.map(prize => [ prize, 0 ]))
                }

                // @ts-ignore
                rating[language][prize] += 1
            })
        })

        return rating
    }, {})
}

export function getLanguageRating(): LanguageRating {
    return Object
        .entries(getLanguageRatingObject())
        .sort(([ , b ], [ , a ]): number => {
            return ((a.first || 0) - (b.first || 0)) +
                ((a.second || 0) - (b.second || 0)) / 100 +
                ((a.third || 0) - (b.third || 0)) / 10000 +
                ((a.lightning || 0) - (b.lightning || 0)) / 1000000
        })
}

export function getClaim(prize: LanguagePrize, name: string): string {
    const names = name.split(", ")

    const claim = names.length > 1
        ? claims.plural[prize]
        : claims.singular[prize]

    return `${name} ${claim}`
}