import {HubConnectionBuilder, LogLevel} from "@microsoft/signalr";
import {events} from "../consts/api";
import {uuid} from "uuidv4";
import createQueryString from './createQueryString';


export default function listenEvents(apiKey: string, eventType: string, handler: (payload: any) => void) {

    let closed = false;
    const requestUrl = `${events}?${createQueryString({sticky: uuid(), apiKey: apiKey})}`

    const connection = new HubConnectionBuilder()
        .withUrl(requestUrl)
        .withAutomaticReconnect()
        .configureLogging(LogLevel.Information)
        .build();

    connection.onclose(async () => {
        console.log("closed");
        if (!closed)
            setTimeout(() => start(), 5000);
    });

    connection.on("onEvent", (eventType, payload) => {
        if (closed)
            return;
        console.log(eventType, payload);
        handler(payload);
    });

    async function start() {
        if (closed)
            return;

        try {
            await connection.start();
            console.log("connected");
        } catch (err) {
            console.log("failed to start", err);
            setTimeout(() => start(), 5000);
            return;
        }

        try {
            await connection.send("subscribe", eventType);
            console.log(`subscribed to ${eventType}`);
        } catch (err) {
            console.log("failed to subscribe", err);
            connection.stop();
        }

    }

    start();

    return {
        close: function () {
            closed = true;
            connection.stop();
        }
    };
}
